import * as React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import { CornerView } from '../corner';

function SignupView() {
    return <div className="pannel">
        <Link to="/passport/login"><CornerView></CornerView></Link>
        <input className="passport-ipt" placeholder="" type="text" />
        <input className="passport-ipt" placeholder="" type="password" />
        <button className="passport-btn"></button>
    </div>;
}

export const Signup = connect()(SignupView);



